  addGeofence(dd) {
  console.log(parseFloat(dd.lat));
  //options describing geofence
  let fence = {
    id: '69ca1b88', //any unique ID
    latitude:       parseFloat(dd.lat), //center of geofence radius
    longitude:      parseFloat(dd.lon),
    radius:         50, //radius to edge of geofence in meters
    transitionType: 3, //see 'Transition Types' below
    notification: { //notification settings
        id:             2, //any unique ID
        title:          'test fogon Prov', //notification title
        text:           'test de geofence fogon de providencia', //notification body
        openAppOnClick: true //open app when notification is tapped
    }
  }

  window.geofence.addOrUpdate(fence).then(
     () => {
       console.log('Geofence added');
       this.status='Geofence added';
       //this.activar2();
     },
     (err) => {
       console.log('Geofence failed to add');
       this.status='Geofence failed to add';
     }
   );
}

